export default {
    mounted() {
        this.$refs.inputEl.addEventListener('keyup', this.onKeyup);
    },
    methods: {
        focus() {
            this.$el.focus();
        },
        onKeyup(e) {
            if(e.target.innerText === '' && e.key === 'Backspace') {
                console.log(e);
                this.$emit('selfDestruct');
            }
        },
    },
}