<template>
    <div id="app">

        <div class="editor">
            <div class="blocks" ref="blocks">
                <block-renderer
                        v-for="(block, i) in blocks"
                        :key="renderKey + '-' + i"
                        :block-type="block"
                        :index="i"
                        :ref="'block' + i"
                        :content="blockContents.hasOwnProperty(i) ? blockContents[i] : null"
                        @keydown="onKeydown($event.event, $event.index)"
                        @setContent="setContent($event)"
                        @addBlockAfter="addBlockAfter($event, 'paragraph')"
                        @deleteBlock="deleteBlock">
                </block-renderer>
            </div>

            <br>

            <button @click="addBlock('Heading')">Add heading</button>
            &nbsp;
            <button @click="addBlock('Paragraph')">Add paragraph</button>
            &nbsp;
            <button @click="addBlock('BlockQuote')">Add quote</button>
        </div>

    </div>
</template>

<script>

    import Sortable from 'sortablejs';

    import BlockRenderer from './components/BlockRenderer';

    export default {
        name: 'app',
        components: {
            BlockRenderer,
        },
        data() {
            return {
                blocks: [],
                blockContents: [],
                renderKey: 1,
            }
        },
        mounted() {
            this.loadblockContents();

            setTimeout(() => {
                const sortable = Sortable.create(this.$refs.blocks, {
                    handle: '.handle',
                    direction: 'vertical',
                });
            })
        },
        methods: {
            loadblockContents() {
                const htmlStr = `
                    <h1>Organically grow the holistic world view</h1>
                    <p>Bring to the table win-win survival strategies to ensure proactive domination. At the end of the day, going forward, a new normal that has evolved from generation X is on the runway heading towards a streamlined cloud solution. User generated content in real-time will have multiple touchpoints for offshoring.</p>
                    <p>Credibly innovate granular internal or "organic" sources whereas high standards in web-readiness.</p>
                    <blockquote>Really inspiring quote...</blockquote>
                    <p>Appropriately empower dynamic leadership skills after business portals. Globally myocardinate interactive supply chains with distinctive quality vectors. Globally revolutionize global sources through interoperable services.</p>
                    <h1>Nanotechnology immersion along the information highway</h1>
                    <p>Synergistically evolve 2.0 technologies rather than just in time initiatives. Quickly deploy strategic networks with compelling e-business. Credibly pontificate highly efficient manufactured products and enabled data.</p>
                    <p>Globally incubate standards compliant channels before scalable benefits. Quickly disseminate superior deliverables whereas web-enabled applications. Quickly drive clicks-and-mortar catalysts for change before vertical architectures.</p>
                    `;
                const doc = new DOMParser().parseFromString(htmlStr, 'text/html');

                for(const node of doc.body.children) {
                    const text = node.innerText;
                    let blockType = node.nodeName;
                    if(blockType === 'P') {
                        blockType = 'paragraph';
                    } else if(blockType === 'H1') {
                        blockType = 'heading';
                    } else if(blockType === 'Ul') {
                        blockType = 'unordered-list';
                    } else if(blockType === 'BLOCKQUOTE') {
                        blockType = 'block-quote';
                    }

                    this.blockContents.push(text);
                    this.blocks.push(blockType);
                }
                this.renderKey++;
            },
            addBlock(blockTypeId) {
                this.blocks.push(blockTypeId);
                this.blockContents.push('');
                this.focusBlock(this.blocks.length - 1);
                this.renderKey++;
            },
            addBlockAfter(index, blockTypeId) {
                console.log(index, blockTypeId);
                this.blockContents.splice(index + 1, 0, '');
                this.blocks.splice(index + 1, 0, blockTypeId);
                this.focusBlock(index + 1);
                this.renderKey++;
            },
            focusBlock(index) {
                setTimeout(() => {
                    let el = this.$refs['block' + index];
                    if(Array.isArray(el)) {
                        el = el[0];
                    }
                    if(el) el.focus();
                })
            },
            deleteBlock(index) {
                this.blocks.splice(index, 1);
                this.blockContents.splice(index, 1);
            },
            setContent(data) {
                this.blockContents[data.index] = data.content;
            }
        },
    }
</script>

<style lang="scss">
    #app {
        font-family: 'Avenir', Helvetica, Arial, sans-serif;
        -webkit-font-smoothing: antialiased;
        -moz-osx-font-smoothing: grayscale;
        color: #2c3e50;

        .editor {
            margin: 100px auto;
            max-width: 800px;
            padding: 30px;
            border: 1px solid #DDD;
        }

        .editable {
            outline: none;
        }
    }
</style>
