<template>
    <div>
        <div class="block-wrapper">
            <div class="handle">#</div>
            <component
                    contenteditable="true"
                    v-bind:is="blockType"
                    @keydown.native="onKeydown"
                    @selfDestruct="deleteBlock"
                    @input="onInput"
                    :initial-content="content"
                    ref="block">
            </component>
        </div>

    </div>
</template>

<script>

    import Heading from './Heading';
    import Paragraph from './Paragraph';
    import UnorderedList from './UnorderedList';
    import BlockQuote from './BlockQuote';

    export default {
        name: "BlockRenderer",
        props: ['blockType', 'index', 'content'],
        components: {
            Heading,
            Paragraph,
            UnorderedList,
            BlockQuote,
        },
        methods: {
            onKeydown(e) {
                if(e.key === 'Enter' && !e.shiftKey) {
                    e.preventDefault();
                    e.stopPropagation();
                    this.$emit('addBlockAfter', this.index);
                }
            },
            focus() {
                this.$refs.block.focus();
            },
            deleteBlock() {
                this.$emit('deleteBlock', this.index);
            },
            onInput(html) {
                this.$emit('setContent', {
                    index: this.index,
                    content: html,
                })
            },
        },
    }
</script>

<style lang="scss" scoped>

    .block-wrapper {
        border: 1px solid transparent;
        padding: 10px;
        display: flex;
        align-items: center;

        .handle {
            flex: 0 0 30px;
            cursor: move;
            cursor: -webkit-grabbing;
            opacity: .5;

            &:hover {
                opacity: 1;
            }
        }

        &:hover {
            //border-color: #DDD;
        }
    }

</style>